.nav-ul li{
display:  inline-block;
padding: 10px;
}
.nav-ul li a{
  color: #fff;
  padding: 5px;
  text-decoration: none;

}
.nav-ul {
 margin: 0;
  padding: 5px;
background-color: skyblue;

}


.inputBox{
  margin: 25px;
  display: block;
  padding: 7px;
  width: 300px;
  border:  1px solid skyblue;

}
.logo{
  width: 45px;
  float: left;
  border-radius: 10px;
  margin-left: 20px;
  margin-top: 3px;
}
.nav-right{
  text-align: right;
}
.register, .product{
  margin-left: 30%;
  height: 100vh;
}
.btn{
  margin: 25px;
  padding: 7px 5px;
  width: 150px;
  border:  1px solid ;
  background-color: skyblue;
  cursor: pointer;

}

.button{
  margin-left: 5px;
}
.update-link{
  text-decoration: none;
  color: black;

}
.login{
  margin-left: 30%;
  margin-top: 50px;
  height: 100vh;
}
.invalid-input{
  color: red;
  margin-top: -20px;
  margin-left: 25px;
  display: block;
}
table  {
text-align: center;
margin: auto;

}
table th{
  border: 1px solid skyblue;
  padding: 10px;
  margin: 5px;
}
table  tr td{
  padding: 10px;
  border: 1px solid skyblue;
  margin: 5px;
}
.product-head  {
text-align: center;

}
.product-div{
  height: 100vh;
}
.product{
  height: 100vh;
}
.footer{
  position: sticky;
bottom: 0;
display: flex;
justify-content: center;
align-items: center;
  width: 100%;
  height: 50px;
  background-color: skyblue;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
